@import "assets/scss/variables.scss";

.wrapper {
    background-color: $bg;
    padding: 5rem 0;
    & .logo {
        margin-right: 5rem;
        & img {
            height: 5rem;
        }
    }
    & .footer-links {
        & li {
            margin-bottom: 0.5rem;
            color: rgba($light, 0.75);
            &:first-child {
                font-size: $textLarge;
                font-weight: 500;
                margin-bottom: 1rem;
                color: $light;
            }
            & a {
                color: $light;
                text-decoration: none;
                &:hover {
                    color: $secondary;
                }
            }
            & svg {
                height: 1rem;
                width: 1rem;
                fill: $secondary;
                margin-right: 0.5rem;
            }
        }
    }
}